import {commonParams} from './config'
import axios from 'axios'

//
export function getLyric(mid) {
  const url = 'http://ustbhuangyi.com/music/api/lyric?g_tk=1928093487&inCharset=utf-8&outCharset=utf-8&notice=0&format=json&platform=yqq&hostUin=0&needNewCode=0&categoryId=10000000&pcachetime=1528867301193'

  const data = Object.assign({}, commonParams, {
    songmid: mid,
  })

  return axios.get(url, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}



