import jsonp from '../common/js/jsonp'
import {commonParams, options} from './config'
import axios from 'axios'

//
export function getRecommend() {
  const url = 'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg'

  //Object.assign({},obj,obj1)1
  //2.
  //3.
  //objobj1
  const data=Object.assign({},commonParams,{
    platform: 'h5',
    uin: 0,
    needNewCode: 1
  });

  return jsonp(url,data,options);
}

// 
export function getPlayList() {
  const url = 'http://ustbhuangyi.com/music/api/getDiscList?g_tk=1928093487&inCharset=utf-8&outCharset=utf-8&notice=0&format=json&platform=yqq&hostUin=0&sin=0&ein=29&sortId=5&needNewCode=0&categoryId=10000000&rnd=0.6327698330987483'
  // const data = Object.assign({}, commonParams, {
  //   platform: 'yqq',
  //   hostUin: 0,
  //   sin: 0,
  //   ein: 29,
  //   sortId: 5,
  //   needNewCode: 0,
  //   categoryId: 10000000,
  //   rnd: Math.random(),
  //   format: 'json'// 
  // })

  return axios.get(url).then(res => {
    return Promise.resolve(res.data)
  })
}

export function getSongList(disstid) {
  const url = 'http://ustbhuangyi.com/music/api/getCdInfo?g_tk=1928093487&inCharset=utf-8&outCharset=utf-8&notice=0&format=jsonp&type=1&json=1&utf8=1&onlysong=0&platform=yqq&hostUin=0&needNewCode=0'

  const data = Object.assign({}, commonParams, {
    disstid,
  })

  return axios.get(url, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}



