
<template>
 <div class="singer" ref="singer">
   <!--@select="selectSinger"listviewitem-->
 <listview @select="selectSinger"  v-bind:data="singers" ref="list"></listview>
   <router-view></router-view>
 </div>
</template>

<script typr="text/ecmascript-6">
  import {getSingerList} from "../../api/singer"
  import {ERR_OK} from "../../api/config";
  import Singer from '../../common/js/singer'
  import Listview from '../../base/listview/listView'
  import { mapMutations } from 'vuex'
  import {playlistMixin} from "../../common/js/mixin";
  //
  const HOT_SINGER_LEN = 10
  const HOT_NAME = ''

    export default {
      name: "singer",
      mixins:[playlistMixin],
      data(){
          return{
            singers:[]
          }
      },
      components:{
        Listview
      },
      created(){
          this._getSingerList()
       },

      methods:{
        // Vuex  store  mutation
        // this.$store.commit('xxx')  mutation mapMutations  methods  store.commit 
        ...mapMutations({
          setSinger:'SET_SINGER'// `this.setSinger(singer);`  `this.$store.commit('SET_SINGER', amount)
        }),

        //
        selectSinger(singer){
          this.$router.push({
            path:`/singer/${singer.id}`
          })
          //itemVuex  store 
          //androidsp
          this.setSinger(singer);
        },

        //
        _getSingerList(){
          getSingerList().then((res)=>{
            if(res.code==ERR_OK){
              this.singers=this._normalizeSinger(res.data.list);
            }
          })
        },

         //scrool
        handlePlaylist(playlist) {
          const bottom = playlist.length > 0 ? '60px' : ''
          this.$refs.singer.style.bottom = bottom
          this.$refs.list.refresh()
        },
        //
        //qq
        //10
        _normalizeSinger(list) {
          //map
          let map = {
            //
            hot: {
              title: HOT_NAME,
              items: []
            }
          }

          //
          list.forEach((item,index)=>{
            //10
            if (index < HOT_SINGER_LEN) {
              map.hot.items.push(new Singer({
                name: item.Fsinger_name,
                id: item.Fsinger_mid
              }))
            }

            //
            const key = item.Findex
            //
            if (!map[key]) {
              map[key] = {
                title: key,
                items: []
              }
            }
            //
            map[key].items.push(new Singer({
              name: item.Fsinger_name,
              id: item.Fsinger_mid
            }))
          })

          //  map
          let ret = []
          let hot = []
          //map
          for (let key in map){
            //
            let val = map[key]
            //
            if (val.title.match(/[a-zA-Z]/)) {
              ret.push(val)
            }
            //
            else if (val.title === HOT_NAME) {
              hot.push(val)
            }
          }

          //
          ret.sort((a, b) => {
            return a.title.charCodeAt(0) - b.title.charCodeAt(0)
          })

          //
          return hot.concat(ret)
        }
      }

    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.singer{
  position: fixed
  top:88px
  bottom :0
  width :100%
}
</style>



